/*  :  */
export function getSearchMovie(keyword: string) {
  return fetch(
    `${BASE_PATH}/search/movie?api_key=${API_KEY}&include_adult=true&query=${keyword}`
  ).then((response) => response.json());
}

/*  : Tv */
export function getSearchTv(keyword: string) {
  return fetch(
    `${BASE_PATH}/search/tv?api_key=${API_KEY}&include_adult=true&query=${keyword}`
  ).then((response) => response.json());
}
