import { useQuery } from 'react-query';
import { Helmet } from 'react-helmet';
import { AnimatePresence } from 'framer-motion';
import { PathMatch, useNavigate, useMatch } from 'react-router-dom';
import {
  IGetTvResult,
  getTvLatest,
  getTvAiringToday,
  getTvOnTheAir,
  getTvPopular,
  getTvTopRated,
} from '../api';
import { makeImagePath } from '../utils';
import { Wrapper, Loader, Banner, Title, Overview } from '../design';
import TvSlider from '../Components/TvSlider';
import TvModal from '../Components/TvModal';

function Tv() {
  const navigate = useNavigate();
  const bigTvMatch: PathMatch<string> | null = useMatch('/tv/:tvId');
  const tvId = bigTvMatch?.params.tvId;

  const useMultipleQuery = () => {
    const latest = useQuery<IGetTvResult>(['latest'], getTvLatest);
    const airingToday = useQuery<IGetTvResult>(
      ['airingToday'],
      getTvAiringToday
    );
    const onTheAir = useQuery<IGetTvResult>(['onTheAir'], getTvOnTheAir);
    const popular = useQuery<IGetTvResult>(['popular'], getTvPopular);
    const topRated = useQuery<IGetTvResult>(['topRated'], getTvTopRated);
    return [latest, airingToday, onTheAir, popular, topRated];
  };
  const [
    { data: latestData, isLoading: latestLoading },
    { data: airingTodayData, isLoading: airingTodayLoading },
    { data: onTheAirData, isLoading: onTheAirLoading },
    { data: popularData, isLoading: popularLoading },
    { data: topRatedData, isLoading: topRatedLoading },
  ] = useMultipleQuery();

  const loading =
    latestLoading ||
    airingTodayLoading ||
    onTheAirLoading ||
    popularLoading ||
    topRatedLoading;
  return (
    <Wrapper>
      <Helmet>
        <title>Netflix Clone-ggomdong</title>
      </Helmet>
      {loading ? (
        <Loader>Loading...</Loader>
      ) : (
        <>
          <Banner
            bgPhoto={makeImagePath(
              airingTodayData?.results[0].backdrop_path || ''
            )}
          >
            <Title>{airingTodayData?.results[0].name}</Title>
            <Overview>{airingTodayData?.results[0].overview}</Overview>
          </Banner>
          <TvSlider
            data={airingTodayData!.results}
            name="Airing Today"
            category="nowPlaying"
          ></TvSlider>
          <TvSlider
            data={onTheAirData!.results}
            name="On The Air"
            category="onTheAir"
          ></TvSlider>
          <TvSlider
            data={popularData!.results}
            name="Popular"
            category="popular"
          ></TvSlider>
          <TvSlider
            data={topRatedData!.results}
            name="Top Rated"
            category="topRated"
          ></TvSlider>
          <AnimatePresence>
            {bigTvMatch ? <TvModal tvId={+tvId!} requestUrl="/tv" /> : null}
          </AnimatePresence>
        </>
      )}
    </Wrapper>
  );
}

export default Tv;
