import { useQuery } from 'react-query';
import { Helmet } from 'react-helmet';
import { AnimatePresence } from 'framer-motion';
import { PathMatch, useNavigate, useMatch } from 'react-router-dom';
import {
  IGetMoviesResult,
  getMovieLatest,
  getMovieNowPlaying,
  getMovieTopRated,
  getMovieUpcoming,
} from '../api';
import { makeImagePath } from '../utils';
import { Wrapper, Loader, Banner, Title, Overview } from '../design';
import MovieSlider from '../Components/MovieSlider';
import MovieModal from '../Components/MovieModal';

function Home() {
  const navigate = useNavigate();
  const bigMovieMatch: PathMatch<string> | null = useMatch('/movies/:movieId');
  const movieId = bigMovieMatch?.params.movieId;

  const useMultipleQuery = () => {
    const latest = useQuery<IGetMoviesResult>(['latest'], getMovieLatest);
    const nowPlaying = useQuery<IGetMoviesResult>(
      ['nowPlaying'],
      getMovieNowPlaying
    );
    const topRated = useQuery<IGetMoviesResult>(['topRated'], getMovieTopRated);
    const upcoming = useQuery<IGetMoviesResult>(['upcoming'], getMovieUpcoming);
    return [latest, nowPlaying, topRated, upcoming];
  };
  const [
    { data: latestData, isLoading: latestLoading },
    { data: nowPlayingData, isLoading: nowPlayingLoading },
    { data: topRatedData, isLoading: topRatedLoading },
    { data: upcomingData, isLoading: upcomingLoading },
  ] = useMultipleQuery();

  const loading = nowPlayingLoading || topRatedLoading || upcomingLoading;
  return (
    <Wrapper>
      <Helmet>
        <title>Netflix Clone-ggomdong</title>
      </Helmet>
      {loading ? (
        <Loader>Loading...</Loader>
      ) : (
        <>
          <Banner
            bgPhoto={makeImagePath(
              nowPlayingData?.results[0].backdrop_path || ''
            )}
          >
            <Title>{nowPlayingData?.results[0].title}</Title>
            <Overview>{nowPlayingData?.results[0].overview}</Overview>
          </Banner>
          <MovieSlider
            data={nowPlayingData!.results}
            title="Now Playing"
            category="nowPlaying"
          ></MovieSlider>
          <MovieSlider
            data={topRatedData!.results}
            title="Top Rated"
            category="topRated"
          ></MovieSlider>
          <MovieSlider
            data={upcomingData!.results}
            title="Upcoming"
            category="upcoming"
          ></MovieSlider>
          <AnimatePresence>
            {bigMovieMatch ? (
              <MovieModal movieId={+movieId!} requestUrl="/" />
            ) : null}
          </AnimatePresence>
        </>
      )}
    </Wrapper>
  );
}

export default Home;
