import {
  Overlay,
  BigMedia,
  BigCover,
  BigTitle,
  BigItem,
  BigPoster,
  BigOverview,
} from '../design';
import { useNavigate } from 'react-router-dom';
import { AnimatePresence, useScroll } from 'framer-motion';
import { useQuery } from 'react-query';
import { IMovie, getMovie } from '../api';
import { makeImagePath } from '../utils';

interface MovieModalProps {
  movieId: number;
  requestUrl: string;
}

function MovieModal({ movieId, requestUrl }: MovieModalProps) {
  const navigate = useNavigate();
  const onOverlayClick = () => navigate(requestUrl);
  const { scrollY } = useScroll();
  const { data } = useQuery<IMovie>('movie', () => getMovie(movieId));
  return (
    <>
      <AnimatePresence>
        {data ? (
          <>
            <Overlay
              onClick={onOverlayClick}
              exit={{ opacity: 0 }}
              animate={{ opacity: 1 }}
            />
            <BigMedia style={{ top: scrollY.get() + 100 }}>
              <>
                <BigCover
                  style={{
                    backgroundImage: `linear-gradient(to top, black, transparent), url(${makeImagePath(
                      data.backdrop_path,
                      'w500'
                    )})`,
                  }}
                />
                <BigTitle>{data.title}</BigTitle>
                <BigItem>
                   : {data.release_date}{' '}
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  : {data.runtime}
                   &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  :{' '}
                  {data.vote_average}
                </BigItem>
                <BigPoster
                  style={{
                    backgroundImage: `linear-gradient(to top, black, transparent), url(${makeImagePath(
                      data.poster_path,
                      'w200'
                    )})`,
                  }}
                />
                <BigOverview>{data.overview}</BigOverview>
              </>
            </BigMedia>
          </>
        ) : null}
      </AnimatePresence>
    </>
  );
}

export default MovieModal;
