import {
  useLocation,
  useNavigate,
  PathMatch,
  useMatch,
} from 'react-router-dom';
import { useQuery } from 'react-query';
import {
  IGetMoviesResult,
  IGetTvResult,
  getSearchMovie,
  getSearchTv,
} from '../api';
import {
  Slider,
  SliderTitle,
  Row,
  Box,
  Info,
  boxVariants,
  infoVariants,
  SearchBanner,
} from '../design';
import { AnimatePresence } from 'framer-motion';
import { makeImagePath } from '../utils';
import MovieModal from '../Components/MovieModal';
import TvModal from '../Components/TvModal';

function Search() {
  const location = useLocation();
  const keyword = new URLSearchParams(location.search).get('keyword');
  const { data: movieData } = useQuery<IGetMoviesResult>('searchMovie', () =>
    getSearchMovie(keyword || '')
  );
  const { data: tvData } = useQuery<IGetTvResult>('searchTv', () =>
    getSearchTv(keyword || '')
  );
  const navigate = useNavigate();
  const onBoxClicked = (category: string, id: number) => {
    navigate(`/search/${category}/${id}`);
  };
  const bigSearchMatch: PathMatch<string> | null = useMatch(
    '/search/:category/:id'
  );
  const category = bigSearchMatch?.params.category;
  const id = bigSearchMatch?.params.id;

  const searchUrl = '/search?keyword='.concat(keyword!);

  return (
    <>
      <SearchBanner />
      <SliderTitle>[Movie] Search Result : {keyword}</SliderTitle>
      <Slider>
        <Row>
          {movieData?.results.map((movie) => (
            <Box
              layoutId={movie.id + movie.title}
              key={movie.id}
              whileHover="hover"
              initial="normal"
              variants={boxVariants}
              onClick={() => onBoxClicked('movie', movie.id)}
              transition={{ type: 'tween' }}
              bgPhoto={makeImagePath(movie.backdrop_path, 'w500')}
            >
              <Info variants={infoVariants}>
                <h4>{movie.title}</h4>
              </Info>
            </Box>
          ))}
        </Row>
      </Slider>
      <SliderTitle>[Tv Show] Search Result : {keyword}</SliderTitle>
      <Slider>
        <Row>
          {tvData?.results.map((tv) => (
            <Box
              layoutId={tv.id + tv.name}
              key={tv.id}
              whileHover="hover"
              initial="normal"
              variants={boxVariants}
              onClick={() => onBoxClicked('tv', tv.id)}
              transition={{ type: 'tween' }}
              bgPhoto={makeImagePath(tv.backdrop_path, 'w500')}
            >
              <Info variants={infoVariants}>
                <h4>{tv.name}</h4>
              </Info>
            </Box>
          ))}
        </Row>
      </Slider>
      <AnimatePresence>
        {bigSearchMatch ? (
          category === 'movie' ? (
            <MovieModal movieId={+id!} requestUrl={searchUrl} />
          ) : (
            <TvModal tvId={+id!} requestUrl={searchUrl} />
          )
        ) : null}
      </AnimatePresence>
    </>
  );
}

export default Search;
